#proj {
	position: relative;
	top: 50px;
}
div {
	margin: 0;
	padding: 0;
}
ul {
	list-style-type: none;
}

.repo-list {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	padding: 20px;
}

.repo-info {
	padding: 20px;
}

.repo-container {
	background-color: white;
	padding: 10px;
	width: auto;
	-webkit-box-shadow: 10px 10px 5px #888888;
	        box-shadow: 10px 10px 5px #888888;
	border: 2px solid #43a5e6;
	border-radius: 100px;
	text-align: center;
	-ms-flex-pack:distribute;
	    justify-content:space-around;
	margin: 15px;
}

.fork {
	font-weight:350;
}

#proj a {
	color: black;
}

#proj a:hover {
	text-decoration: none;
}

.repo-name {
	font-size: 40px;
	font-family:monospace;
}

.repo-name:hover {
	font-weight: bold;
}

.repo-link {
	color: #2881d7;
	font-size: 18px;
	font-size: 18px;
}

.repo-link:hover {
	font-weight: bold;
	font-size: 19px;
}



